
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import cookie from 'vux/src/tools/cookie/index.js'

import { formateDate, isSpace, isMobile } from '../utils/validate'
import { mapState } from 'vuex'
import api from '../api'
export default {
name: 'column',
data () {
return {
navs: [],
id: this.$route.params.id,
resSubmit: {
result: '1',
status: false,
type: 'success',
tips: '提交成功'
},
isHidden: '0',
title: '',
content: '',
name: '',
mobile: ''
}
},
components: {
Group,
XButton,
XInput,
XTextarea,
Toast,
Checker,
CheckerItem,
Loading
},
created () {
this.$store.dispatch({type: 'getLists', url: api.getLists(this.id)})
this.getBreadcrumb()
},
computed: {
...mapState([
'msg',
'checkerArr',
'lists',
'isLoad'
])
},
methods: {

getBreadcrumb () {
let matched = this.$route.matched.filter(item => item.name)
let sub = JSON.parse(cookie.get('sub'))
const first = matched[0]
if (first && first.name !== 'index') {
matched = [{ path: '/', name: 'index', meta: {title: '首页'} }].concat([{meta: {title: sub.name}}])
}
this.navs = matched
},

hideToast () {
if (this.resSubmit.result === '1') {
this.$router.push({ path: '/' })
}
},

submitForm () {

if (isSpace(this.title) || isSpace(this.content)) {
return false
}

if (this.isHidden === '0') {
if (isSpace(this.name) || isMobile(this.mobile)) {
return false
}
}
const json = {
'zt_topic_labelid': this.id,
'ishidden': this.isHidden,
'title': this.title,
'content': this.content,
'name': this.name,
'mobile': this.mobile
}
this.$store.dispatch({type: 'postForm', url: api.postForm, data: json}).then(res => {
let temp = res.data
let tempType = temp.result === '1' ? 'success' : 'cancel'
this.resSubmit = {
result: temp.result,
status: true,
type: tempType,
tips: temp.errorInfo
}
})
}
},
filters: {
parseTime (val) {
return formateDate(val)
}
}
}



